@observer
class SongPanel extends Component {
    constructor(props) {
        super(props);
        this.state = {
            songUrl: "",
        }
    }
    playSong = (e) => {
       if(e.key === "Enter" ){
           console.log( this.state.songUrl)
           this.props.rootStore.playSong(this.state.songUrl)
           this.setState({songUrl: ''})
       }
    }



    render() {
        const {rootStore} = this.props;
        const {progress, duration} = this.state
        return (
            <div>
                <div className={styles.songPanel}>
                    <input className={styles.inputUrl} placeholder={"вставьте URL"} value={this.state.songUrl} onChange={(e)=> this.setState({songUrl: e.target.value})} onKeyDown={this.playSong}/>
                    <button className={styles.controls} onClick={this.playSong}>+</button>
                </div>
                {rootStore.songs.length > 0?
                    <div className={styles.songContainer}>
                        <Song song = {rootStore.songs[0]}/>
                    </div>: null}
            </div>
        );
    }
}

export default SongPanel;