export const convertTime = (sec) => {
    let hours = Math.floor(sec/60/60);
    let minutes = Math.floor(sec/60) - hours * 60;
    let seconds = Math.floor(sec%60);

    let formatted = [
        hours.toString(),
        minutes.toString(),
        seconds.toString()
    ];
    if (hours === 0) {
        return `${formatted[1]}:${formatted[2].padStart(2, "0")}`
    }
    return `${formatted[0].padStart(2, "0")}:${formatted[1].padStart(2, "0")}:${formatted[2].padStart(2, "0")}`
}