import {inject, observer} from "mobx-react";
import React, {Component} from "react";
import SongPanel from "../SongPanel/songPanel";
import Queue from "../Queue";
import styles from "./index.css";
import Addons from "../Addons";

@inject("rootStore")
@observer
class MusicConsole extends Component {
    constructor(props) {
        super(props);
        this.state = {
            queue: null,
            ws: null
        }
    }

    componentDidMount() {
        this.connect();
        this.checkQueue();
    }

    connect = () => {
        const ws = new WebSocket(`wss://dsbotweb.herokuapp.com`)
        let that = this;
        let connectInterval
        ws.onopen = () => {
            console.log("connected websocket");

            this.setState({ ws: ws });

            that.timeout = 250; // reset timer to 250 on open of websocket connection
            clearTimeout(connectInterval); // clear Interval on on open of websocket connection
        };

        ws.onclose = e => {
            console.log(
                `Socket is closed. Reconnect will be attempted in ${Math.min(
                    10000 / 1000,
                    (that.timeout + that.timeout) / 1000
                )} second.`,
                e.reason
            );

            that.timeout = that.timeout + that.timeout; //increment retry interval
            connectInterval = setTimeout(this.check, Math.min(10000, that.timeout)); //call check function after timeout
        };

        ws.onmessage = evt => {
            // listen to data sent from the websocket server
            const message = JSON.parse(evt.data)
            if(message.queue){
                this.props.rootStore.checkQueue(message);
            }
        }

        ws.onerror = err => {
            console.error(
                "Socket encountered error: ",
                err.message,
                "Closing socket"
            );

            ws.close();
        };
    }

    checkQueue = async () => {
        const res = await fetch('/api/queue')
        const data = await res.json()
        this.props.rootStore.checkQueue(data);
    }

    render() {

        return (
            <div>
                <SongPanel rootStore={this.props.rootStore}/>
                <div className={styles.addonsContainer}>
                    <Queue rootStore = {this.props.rootStore}/>
                    <Addons rootStore = {this.props.rootStore}/>
                </div>

            </div>
        );
    }
}
export default MusicConsole